.App {
  text-align: center;
  width: 100%;
  height: 100vh;
  background-color: #1b231d;
  margin: 0;
  display: flex;
  align-items: center;
}

.Circle {
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  flex-direction: column;
  width: 80vh;
  height: 80vh;
  border-radius: 50%;
  border-color: transparent;
  border-style: solid;
  border-width: 35px;
  margin: auto;
}

.Timer {
  font-size: 16vh;
  margin: 0;
  color: white;
}

.Desc {
  font-size: 3vh;
  margin: 0;
  color: white;
}

.TotallyReasonable {
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
}

.base-timer__svg {
  color: rgb(65, 184, 131);
  width: 90vh;
  height: 90vh;
}

.base-timer__circle {
  fill: none;
  stroke: none;
}

.base-timer__path-elapsed {
  stroke-width: 5px;
  stroke: #283029;
}

.base-timer__path-remaining {
  stroke-width: 5px;
  stroke-linecap: round;
  transform: rotate(90deg);
  transform-origin: center;
  transition: 1s linear all;
  fill-rule: nonzero;
  stroke: currentColor;
}

.base-timer__path-remaining.green {
  color: rgb(65, 184, 131);
}

.base-timer__path-remaining.orange {
  color: orange;
}

.base-timer__path-remaining.red {
  color: red;
}

.ATotallyFairAbsoluteButtonPositioner {
  z-index: 4;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  flex-direction: column;
  width: auto;
  height: calc(100% - 40px*2);
  margin: 40px;
  gap: 10px;
}

.ATotallyReasonableButton {
  width: 24vh;
  height: 100%;
  background-color: #ffffff10;
  border-style: none;
  color: white;
  font-size: 32pt;
}

.TOPCAP {
  border-radius: 15px 15px 10px 10px;
}

.MIDDLEBUTTON {
  border-radius: 10px;
}

.BOTTOMCAP {
  border-radius: 10px 10px 15px 15px;
}